	module.exports = {"data":{"markdownRemark":{"html":"<h1>Units Matter</h1>\n<p>A negotiator with power and leverage has a better chance of reaching a favorable outcome.\nThe <em>percentage mask</em>, can be used to gain a leg up during the negotiation.\nUsing a simple rule of thumb, the technique can be employed with ease.\nIt can be used offensively or defensively.</p>\n<h1>The Percentage Mask</h1>\n<blockquote>\n<p>Percentage Masks are caused by dissonance between perceived values and actual values.</p>\n</blockquote>\n<p>Numbers can be framed in many different ways.\nA 5% raise on a 50k base can be expressed as: +5k; 55k; 10%; $2115 per paycheck; +$115 per paycheck.\nEach variation conveys the value differently, and carries a unique perception.\nDecisions hinge off the perception of the offer.</p>\n<p>Typically, a recipient of a raise deserves it, and the point of contention is the amount.\nA 10% raise could feel like a win, but a bump from 50k to 55k for an increased workload may feel like a ripoff.\nBoth are the same value, but the perception of the value differs.\nThis idiosyncrasy of our perception classified as a cognative bias called the <a href=\"https://en.wikipedia.org/wiki/Framing_effect_(psychology)\">The Framing Effect</a>.</p>\n<p>The Framing Effect can be especially dangerous when only one of the frames is presented to the decision maker.\nA <em>percentage mask</em> occurs when the percentage option is presented in isolation to a negotiating party.\nIt alters the power balance because the value has varied appeal compared to its real number form.</p>\n<p>Luckily, we can usually mentally approximate percentages.\nHowever, the translation is not for free.\nCalculations like converting percentages to real numbers have an associated <a href=\"https://en.wikipedia.org/wiki/Mental_accounting\">Mental Accounting Cost</a>.\nOur brains will allocate resources to translation, which could be used to maximize its negotiating position.</p>\n<p>When the real number bases are equal, another danger occurs.\n10% 10k seem equal until our brain converts the 10% to a real number.\nSince it takes our brain some time to grok a percentage, it falls susceptible to confusing its value.\n10% gets perceived as equal to 10k, even if the 10% (of 50k) is only 5k.\nThis is classified as another cognitive bias, <a href=\"https://en.wikipedia.org/wiki/Money_illusion\">The Money Illusion</a>.\nIn conjunction with the Framing Effect, this makes lone percentages especially powerful in negotiations.</p>\n<p>With these concepts in mind, it is important to understand the perception of percentages in play during negotiations.\nDr. Timothy Heath et al. study the effects of percentages in discounts.\nThey recommend: 'if proportional (percentage) discounts are large, state them. If not, do not state them.' <a href=\"http://jcr.oxfordjournals.org/content/jcr/22/1/90.full.pdf\">see here</a>.\nAs such, this rule should be employed in negotiations:\n<em>Seekers should solicit percentages when values are greater than 100, and the opposite is true for those offering.</em>\nThe table below outlines the possibilities:</p>\n<h1>Grid For Unit Based Percentage Masking</h1>\n<p>This is the most important takeaway from the article.\nEverything else is explaining why this table is true.</p>\n<table>\n<thead>\n<tr>\n<th>Paradigm</th>\n<th>Unit > 100</th>\n<th>Unit &#x3C; 100</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Giving</td>\n<td>Actual</td>\n<td>Percentage</td>\n</tr>\n<tr>\n<td>Receiving</td>\n<td>Percentage</td>\n<td>Actual</td>\n</tr>\n</tbody>\n</table>\n<p>In negotiations, whenever possible, use the favorable unit.\nIf offered a 10% raise on 50k, counter with a 7k ask not 12%.</p>\n<h1>Bonus: Manipulability Indicator</h1>\n<p>Initial offers leave the negotiation at a crossroads.\nWill the counter offer be in a percentage or a static unit?\nIt is up to the receiver to properly execute the next move.</p>\n<p>Failure to do so, sends a signal of manipulability.\nConversely, properly converting an offer shows, shows prowess in bargaining skills.\nThe person translating to a favorable representation offers a question to the opposition:\n\"Will you concede to my power?\"\nReplying in favorable units conveys: \"You tried to distort my reality, and I do not accept the distortion.\nIf you try it again, I will do the same.\"\nThus offering a warning to the opposed party to try manipulation tactics.</p>\n<h1>Conclusion</h1>\n<p>Using the <em>percentage mask</em> as a mental model should help guide negotiations and eek out extra points each time.\nAt the very least, it should save a few points to someone else employing similar tactics.\nWhen a negotiating party is oblivious to the handicap the effect is amplified.\nUse the table provided as a rule of thumb to guide your unit choice in negotiations.</p>\n<h1>Resources</h1>\n<ol>\n<li><a href=\"http://biaslist.com\">http://biaslist.com</a> A Great Site Explaining Cognitive Biases</li>\n<li><a href=\"http://jcr.oxfordjournals.org/content/jcr/22/1/90.full.pdf\">Mental Accounting and Changes in Price: The Frame Dependence of Reference Dependence</a></li>\n<li><a href=\"https://en.wikipedia.org/wiki/Framing_effect_(psychology)\">Wikipedia: Framing Effect</a></li>\n<li><a href=\"https://en.wikipedia.org/wiki/Mental_accounting\">Wikipedia: Mental Accounting</a></li>\n<li><a href=\"https://en.wikipedia.org/wiki/Money_illusion\">Wikipedia: Money Illusion</a></li>\n</ol>","frontmatter":{"title":"Percentage Masking"},"fields":{"date":"2015-08-31"}}},"pathContext":{"url":"/posts/2015-08-31-percentage-masking/"}}

